module.exports = "\n.notify-container {\n    transition: all 500ms;\n    cursor: pointer;\n    box-shadow: 0 10px 30px -12px rgba(0, 0, 0, 0.42), 0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);\n    position: fixed;\n    width: 250px;\n    max-width: 500px;\n    top: 20px;\n    right: 30px;\n    z-index: 99;\n    border: none;\n    outline: none;\n    padding: 12px;\n    border-radius: 10px;\n    font-size: 17px;\n    -webkit-animation: notificationPop 1s;\n            animation: notificationPop 1s;\n    background-color: rgba(241, 10, 21, 0.897);   \n    color: white;  \n}\n\n@-webkit-keyframes notificationPop {\n    0% {opacity: 0;-webkit-transform: translateX(120%);transform: translateX(120%);}\n    100% {opacity: 1;-webkit-transform: translateX(0);transform: translateX(0);}\n}\n\n@keyframes notificationPop {\n    0% {opacity: 0;-webkit-transform: translateX(120%);transform: translateX(120%);}\n    100% {opacity: 1;-webkit-transform: translateX(0);transform: translateX(0);}\n}\n\n@-webkit-keyframes notificationDisappear {\n    0% {opacity: 1;-webkit-transform: translateX(0);transform: translateX(0);}\n    100% {opacity: 0;-webkit-transform: translateX(120%);transform: translateX(120%);}\n}\n\n@keyframes notificationDisappear {\n    0% {opacity: 1;-webkit-transform: translateX(0);transform: translateX(0);}\n    100% {opacity: 0;-webkit-transform: translateX(120%);transform: translateX(120%);}\n}\n\n.notificationDisappear { -webkit-animation: notificationDisappear 1s; animation: notificationDisappear 1s; }\n\n.notify-container:hover { background-color: rgba(241, 10, 21, 0.575); }\n\n.notify-icon {\n    color: white;\n    float:left;\n    font-size: 110%;\n    margin-right: 15px;\n}\n\n/*\n.notify-background {\n    background: rgba(186, 46, 53, .8);    \n    border-right: 10px solid rgba(186, 46, 53, .8);\n    color: rgb(82, 82, 82);\n}\n\n\n.notify-background-white {\n    background: white;    \n    border-right: 10px solid rgba(186, 46, 53, .8);\n    color: rgb(82, 82, 82);\n}\n.notify-icon-background-white {color: rgba(186, 46, 53, .8)!important;} */"